<script>
import { getFirmware, getFirmwareProduct, getFlashListiPhone, getFlashListiPad, getFlashListiPod, getFlashListMac, getinfoDataList, getFlashListAppleTV, getFlashListWatch, getFlashListHomePod, getFlashListVision } from '../../api/firmware.js'
import { mapState } from 'vuex';
export default {

    data() {
        return {
            active: 2,
            isShowGoTopButton: false,
            stepsList: [
                { name: this.$t("firmware.selectProduct"), id: 0 },
                { name: this.$t("firmware.selectPlatform"), id: 1 },
                { name: this.$t("firmware.selectVersion"), id: 2 },
                // { name: '下载固件', id: 3 },
            ],
            firmwareProductList1: [],
            firmwareProductList2: [],
            flashinfoData: [],
            versionTab: '0',
            tabIndex: '0',
            clearfixData:
            {
                time: '2023/9/13',
                type: 'iPhone 15 Pro Max',
                system: 'iOS 17',
                outline: '直板',
                connector: 'USB-C接口',
                color: '黑色钛金属，白色钛金属，蓝色钛金属，原色钛金属',
                size: '159.9x76.7x8.25mm',
                kg: '221g',
                configuration: '装有iOS 17 的iPhone,USB-C连接线，资料'
            },
            firmwareColumn: [
                { name: this.$t("firmware.firmwareColumn1"), property: 'productName', width: '240px', type: 'String' },
                // { name: "产品型号", property: '', width: '200px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn2"), property: 'column2', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn3"), property: 'column4', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn4"), property: 'column3', width: '240px', type: 'String' },
                // { name: "可否越狱", property: 'prisonBreak', width: '180px', type: 'Boole' },
                { name: this.$t("firmware.firmwareColumn5"), property: 'column1', width: '230px', type: 'Boole' },
            ],
            firmwareTableData: []


        }
    },
    created() {
        this.getFirmwareData()
    },

    methods: {
        async getFirmwareData() {
            console.log(this.$route.query)
            let getData = {
                deviceId: this.$route.query.deviceId,
                productNameid: this.$route.query.productNameid
            }
            this.flashinfoData = ''
            let flashListData = {}
            let flashinfoData = {}
            if (getData.deviceId == 1) {
                flashListData = await getFlashListiPhone({ deviceId: getData.deviceId, productNameId: getData.productNameid })
                flashinfoData = await getinfoDataList({ deviceId: getData.deviceId, productNameId: getData.productNameid })
                this.flashinfoData = flashinfoData.res[0] ? flashinfoData.res[0] : {}
            } else if (getData.deviceId == 2) {
                flashListData = await getFlashListiPad({ deviceId: getData.deviceId, productNameId: getData.productNameid })
                flashinfoData = await getinfoDataList({ deviceId: getData.deviceId, productNameId: getData.productNameid })
                this.flashinfoData = flashinfoData.res[0] ? flashinfoData.res[0] : {}
            } else if (getData.deviceId == 7) {
                flashListData = await getFlashListiPod({ deviceId: getData.deviceId, productNameId: getData.productNameid })
                flashinfoData = await getinfoDataList({ deviceId: getData.deviceId, productNameId: getData.productNameid })
                this.flashinfoData = flashinfoData.res[0] ? flashinfoData.res[0] : {}
            } else if (getData.deviceId == 3) {
                flashListData = await getFlashListMac({ deviceId: getData.deviceId, productNameId: getData.productNameid })
            } else if (getData.deviceId == 4) {
                flashListData = await getFlashListVision({ deviceId: getData.deviceId, productNameId: getData.productNameid })
            } else if (getData.deviceId == 5) {
                flashListData = await getFlashListAppleTV({ deviceId: getData.deviceId, productNameId: getData.productNameid })
            } else if (getData.deviceId == 6) {
                flashListData = await getFlashListHomePod({ deviceId: getData.deviceId, productNameId: getData.productNameid })
            } else if (getData.deviceId == 8) {
                flashListData = await getFlashListWatch({ deviceId: getData.deviceId, productNameId: getData.productNameid })
            }
            this.firmwareTableData = flashListData.res ? flashListData.res : {}
            this.active = 2
            this.versionTab = '0'
            window.scrollTo({ top: 0 })
        },
        //tab 选中
        activeClick(data) {
            if (this.active > data.id) {
               if(data.id==0){
                this.$router.push('/firmware/selectProduct');
               }else{
                this.$router.go(-1); 
               }
            }
        },
        //将图片从base编码转成正常
        imageSrc(data) {
            return `data:image/png;base64, ${data}`;
        },
        //表格点击行
        handleCurrentChange(val) {
            console.log(val)
            this.currentRow = val;

        },
        downloadLink(data) {
            if (!data.downloadLink) return
            let url = data.downloadLink;
            // downloadFile(url)
            // function downloadFile(url) {
            //     //下载文件
            console.log(url)
            let a = document.createElement("a");
            a.setAttribute("href", url);
            a.setAttribute("download", data.column2);
            a.setAttribute("target", "_blank");
            let clickEvent = document.createEvent("MouseEvents");
            clickEvent.initEvent("click", true, true);
            a.dispatchEvent(clickEvent);
            // }
        },
        handleScroll() {
            if (window.pageYOffset > 100) { // 当滚动超过100px时显示“返回顶部”按钮
                this.isShowGoTopButton = true;
            } else {
                this.isShowGoTopButton = false;
            }
        },
        goTop() {
            window.scrollTo({ top: 0, behavior: 'smooth' }) // 平滑滚动至顶部
        },
        updateTitle() {
            this.stepsList = [
                { name: this.$t("firmware.selectProduct"), id: 0 },
                { name: this.$t("firmware.selectPlatform"), id: 1 },
                { name: this.$t("firmware.selectVersion"), id: 2 },
                // { name: '下载固件', id: 3 },
            ]
            this.firmwareColumn = [
                { name: this.$t("firmware.firmwareColumn1"), property: 'productName', width: '240px', type: 'String' },
                // { name: "产品型号", property: '', width: '200px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn2"), property: 'column2', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn3"), property: 'column4', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn4"), property: 'column3', width: '240px', type: 'String' },
                // { name: "可否越狱", property: 'prisonBreak', width: '180px', type: 'Boole' },
                { name: this.$t("firmware.firmwareColumn5"), property: 'column1', width: '200px', type: 'Boole' },
            ]
        },
    },
    computed: {
        ...mapState(['language']),
    },
    watch: {
        language(newVal, oldVal) {
            console.log('Language changed. Updating bottom title...');
            if (newVal !== oldVal) {
                this.updateTitle();
            }
        },
    },
    mounted() {

        window.addEventListener('scroll', this.handleScroll)
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.handleScroll);
    },

}
</script>
